import React,{ useState }  from 'react';
import ReactDOM from 'react-dom';
import 'antd/dist/antd.css';
import './index.css';
import { Row, Col,Space,Button,Input,InputNumber,Form } from 'antd';
const layout = {
  labelCol: { span: 8 },
  wrapperCol: { span: 16 },
};
const { TextArea } = Input;

var panel= <div>

<Row gutter={[8,8]} style={{width:640}}>
  <Col span={12} >
    <Space direction="vertical">
    <span>KeyWords</span>
    <TextArea rows= {10}></TextArea>
    </Space>
 
  
  </Col>
  <Col span={12} ><Space direction="vertical">
    <InputNumber>Count</InputNumber>
    </Space></Col>
</Row>

  
</div>

var state ={}

const Demo = ({props}) => {
  const [state] = useState({})

  const onFinish = values => {
    console.log(values);
    state.keywords=values.keywords.split('\n')
    state.step = values.count
    if (state.index==undefined){
      state.index=-1
    }
    for (var i=0;i<state.step;i+=1){
      state.index+=1
      if (state.index>=state.keywords.length){
        return
      }
      openURL(state.keywords[state.index])
    }
    console.log( state);
  };
 
  const openURL =(keyword)=>{
    console.log(keyword)
    var url = "https://www.google.com/search?q="+encodeURI(keyword)
    openWindow(url)
  }
  function openWindow( url )
{
  window.open(url, '_blank');
  window.focus();
}


  return (
   
    <Form {...layout} name="nest-messages" onFinish={onFinish}style={{width:640}} >
      <Row  >
        <Col span ={2} offset={12}> <h3>Search</h3></Col>
      </Row>
      <Row gutter={[8,8]} >
      <Col span={12} >


      <Form.Item name={['keywords']} label="keywords">
        <Input.TextArea rows={20} />
      </Form.Item>
   
 
  
  </Col>
  <Col span={12}>
      <Form.Item name={['count']} label="count" rules={[{ type: 'number', min: 1, max: 50, }]}  defaultValue={5}>
        <InputNumber  defaultValue={5}/>
      </Form.Item>
      
      <Form.Item wrapperCol={{ ...layout.wrapperCol, offset: 8 }}>
        <Button type="primary" htmlType="submit">
          Open
        </Button>
      </Form.Item>

      </Col>
      </Row>
    </Form>

  

  );
};

ReactDOM.render(
  <Demo/>,
  document.getElementById('container'),
);